<template>
	<div class="container">
		<el-row>
			<el-col :span="24" class="institutionTitle">
				<p></p>
			</el-col>
		</el-row>
		<section class="institutionContent">
			<el-row class="institutionContent-filter">
				<!---->
				<el-col :span="23" class="searchInstitution">
					<el-form :inline="true" :model="formInline" class="demo-form-inline">
						<el-form-item>
							<el-select v-model="formInline.region" placeholder="------">
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
							</el-select>
						</el-form-item>
						<el-form-item>
							<el-input v-model="formInline.institutionName" placeholder=""></el-input>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" icon="search" @click="getinstitutionList"></el-button>
						</el-form-item>
					</el-form>
				</el-col>
				<!---->
				<el-col :span="1" @click.native="handleAdd">
					<el-button type="primary" class="addInstitution">
						<i class="fa fa-plus" aria-hidden="true"></i>
						<span></span>
					</el-button>
				</el-col>
			</el-row>
			<!---->
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column label="" width="180" :show-overflow-tooltip="true">
					<template scope="scope">
						<span>{{ scope.row.institutionName }}</span>
					</template>
				</el-table-column>
				<el-table-column label="" width="120">
					<template scope="scope">
						<span>{{ scope.row.institutionType }}</span>
					</template>
				</el-table-column>
				<el-table-column label="" width="180">
					<template scope="scope">
						<span>{{ scope.row.contacts }}</span>
					</template>
				</el-table-column>
				<el-table-column label="" width="180">
					<template scope="scope">
						<span>{{ scope.row.telephone }}</span>
					</template>
				</el-table-column>
				<el-table-column label="" :show-overflow-tooltip="true">
					<template scope="scope">
						<span>{{ scope.row.address }}</span>
					</template>
				</el-table-column>
				<el-table-column label="" width="200" :show-overflow-tooltip="true">
					<template scope="scope">
						<span> {{scope.row.createTime}} </span>
					</template>
				</el-table-column>
				<el-table-column label="">
					<template scope="scope">
						<span size="small" @click="handleEdit(scope.$index, scope.row)" class="editInstitution"><i class="el-icon-edit"></i></span>
					</template>
				</el-table-column>
			</el-table>
			<!---->
			<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
				<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
					<el-form-item label="" prop="institutionName">
						<el-input v-model="addForm.institutionName" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="institutionType">
						<el-select v-model="formInline.institutionType" placeholder="------">
							<el-option label="" value="1"></el-option>
							<el-option label="" value="2"></el-option>
							<el-option label="" value="3"></el-option>
							<el-option label="" value="4"></el-option>
							<el-option label="" value="5"></el-option>
							<el-option label="" value="6"></el-option>
							<el-option label="" value="0"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="contacts">
						<el-input v-model="addForm.contacts" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="telephone">
						<el-input v-model="addForm.telephone" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input type="textarea" v-model="addForm.address"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click.native="addFormVisible = false"></el-button>
					<el-button type="primary" @click.native="addSubmit" :loading="editLoading"></el-button>
				</div>
			</el-dialog>
			<!---->
			<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
				<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
					<el-form-item label="" prop="institutionName">
						<el-input v-model="editForm.institutionName" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="institutionType">
						<el-select v-model="formInline.institutionType" placeholder="------">
							<el-option label="" value="1"></el-option>
							<el-option label="" value="2"></el-option>
							<el-option label="" value="3"></el-option>
							<el-option label="" value="4"></el-option>
							<el-option label="" value="5"></el-option>
							<el-option label="" value="6"></el-option>
							<el-option label="" value="0"></el-option>
						</el-select>
					</el-form-item>
					<el-form-item label="" prop="contacts">
						<el-input v-model="editForm.contacts" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="" prop="telephone">
						<el-input v-model="editForm.telephone" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="">
						<el-input type="textarea" v-model="editForm.address"></el-input>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click.native="editFormVisible = false"></el-button>
					<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
				</div>
			</el-dialog>
			<div class="block" style="text-align: right;margin-top: 20px;">
				<el-pagination layout="prev, pager, next" :total="100">
				</el-pagination>
			</div>
		</section>
	</div>
</template>
<style lang="scss" scoped>
	$bgTiltle:#3c8dbc;
	.container {
		.institutionTitle {
			text-align: left;
			font-size: 22px;
			color: #444;
			height: 60px;
			border-bottom: 4px solid #cdd1da;
			line-height: 60px;
			padding-left: 20px;
		}
		.institutionContent {
			background-color: #fff;
			padding: 10px;
			.institutionContent-filter {
				margin: 16px 10px;
			}
			.searchInstitution {
				float: right;
				text-align: right;
			}
			.editInstitution {
				color: $bgTiltle;
				cursor: pointer;
			}
			.editInstitution:hover {
				text-decoration: underline;
			}
			.el-icon-edit {
				color: $bgTiltle;
				font-size: 13px;
				padding-right: 2px;
			}
			.el-form-item__label {
				font-size: 20px;
				font-weight: bolder;
			}
		}
	}
</style>
<script>
	import util from '../../common/util';
	export default {
		data() {
			return {
				// filter
				formInline: {
					institutionName: '',
					institutionType: ''
				},
				tableData: [{
					institutionName: '',
					institutionType: '',
					contacts: '',
					telephone: '13612346523',
					address: '3399251',
					createTime: util.formatDate.format(new Date(), 'yyyyMMdd hh:mm:ss')
				}, {
					institutionName: '',
					institutionType: '',
					contacts: '',
					telephone: '13612346523',
					address: '3399251',
					createTime: util.formatDate.format(new Date(), 'yyyyMMdd hh:mm:ss')
				}, {
					institutionName: '',
					institutionType: '',
					contacts: '',
					telephone: '13612346523',
					address: '3399251',
					createTime: util.formatDate.format(new Date(), 'yyyyMMdd hh:mm:ss')
				}, {
					institutionName: '',
					institutionType: '',
					contacts: '',
					telephone: '13612346523',
					address: '3399251',
					createTime: util.formatDate.format(new Date(), 'yyyyMMdd hh:mm:ss')
				}],
				addFormVisible: false,
				addLoading: false,
				addFormRules: {
					institutionName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					telephone: [{
						required: true,
						pattern: /^1[345678]\d{9}$/,
						message: '',
						trigger: 'blur'
					}]
				},
				addForm: {
					institutionName: '',
					institutionType: ''
				},
				editFormVisible: false,
				editLoading: false,
				editFormRules: {
					institutionName: [{
						required: true,
						message: '',
						trigger: 'blur'
					}],
					telephone: [{
						required: true,
						pattern: /^1[345678]\d{9}$/,
						message: '',
						trigger: 'blur'
					}]
				},
				editForm: {
					id: 0,
					institutionName: ''
				}

			}
		},
		methods: {
			// formatter
			//			formatDate:function(time){
			//				return  util.formatDate.format(row.createTime, 'yyyyMMdd hh:mm:ss');
			//			},
			getinstitutionList() {
				let param = {
					institutionName: this.formInline.institutionName,
					institutionType: this.formInline.institutionType
				}
				// need queryInstitution api
				console.log('submit!');
			},
			handleEdit(index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			handleDelete(index, row) {
				console.log(index, row);
			},
			handleAdd: function() {
				this.addFormVisible = true;
				this.addForm = {
					institutionName: ''
				}
			},
			editSubmit: function() {
				this.$refs.editForm.validate((valid) => {
					if(valid) {
						this.$confirm("", '', {
							confirmButtonText: '',
							cancelButtonText: '',
							closeOnClickModal: false,
							type: 'warning'
						}).then(() => {
							this.editLoading = true;
							this.param = Object.assign({}, this.editForm);
							// need editInstitution api
							console.log(this.param);
							this.$message({
								type: 'success',
								message: '!'
							});
							this.editFormVisible=false;
							this.editLoading = false;
						})
					}
				})
			},
			addSubmit: function() {
				this.$refs.addForm.validate((valid) => {
					if(valid) {
						this.$confirm("", '', {
							confirmButtonText: '',
							cancelButtonText: '',
							closeOnClickModal: false,
							type: 'warning'
						}).then(() => {
							this.editLoading = true;
							this.param = Object.assign({}, this.addForm);
							// need addInstitution api
							console.log(this.param);
							this.addFormVisible=false;
							this.editLoading = false;
						})
					}
				})
			},
			mounted() {
				this.getinstitutionList();
			}
		}
	}
</script>


